"use client"
import Link from 'next/link'
import { useEffect, useState } from 'react'

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [isDark, setIsDark] = useState(false)

  // Initialize theme from localStorage and sync to <html>
  useEffect(() => {
    const root = document.documentElement
    const stored = localStorage.getItem('darkMode')
    const initialDark = stored === 'true'
    setIsDark(initialDark)
    root.classList.toggle('dark', initialDark)
  }, [])

  const toggleTheme = () => {
    const next = !isDark
    setIsDark(next)
    const root = document.documentElement
    root.classList.toggle('dark', next)
    localStorage.setItem('darkMode', next ? 'true' : 'false')
  }

  return (
    <header className="fixed top-0 left-0 w-full z-50 bg-gray-950 h-25  shadow-lg">
      <div className="max-w-7xl  flex justify-between mx-auto py-1">
        <p className="text-lg md:text-2xl sm:text-xl text-white font-bold flex items-center   mx-5 sm:mx-4 md:mx-8 py-6 sm:py-4 md:py-7">
          <Link href="/">Finance App</Link>
        </p>

        <button
          type="button"
          aria-label="Open menu"
          className={`text-white md:hidden px-10 py-2 focus:outline-none ${isMenuOpen ? 'hidden' : ''}`}
          aria-expanded={isMenuOpen}
          onClick={() => setIsMenuOpen(true)}
        >
          &#9776;
        </button>

        <div
          className={
            `wrapper ${isMenuOpen ? 'flex' : 'hidden'} flex-col rounded-lg shadow-lg mr-0 p-6 h-full w-48 z-50 items-end md:flex md:items-center justify-between gap-4 md:w-auto md:shadow-none md:rounded-none md:flex-row`
          }
        >
          {/* Close button aligned right (mobile only) */}
          <div className="w-full flex justify-end md:hidden mb-2">
            <button
              type="button"
              aria-label="Close menu"
              className="text-red-500"
              onClick={() => setIsMenuOpen(false)}
            >
              x
            </button>
          </div>

          <p className="text-white text-md font-medium sm:px-3 md:px-5 py-3 sm:py-5 md:py-2 hover:bg-gray-900 hover:scale-95 rounded-full md:rounded m-0 md:m-0 md:w-auto w-full">
            <a href="/totalamounts" target="_self">Total Amounts</a>
          </p>

          <button
            type="button"
            className="text-white text-md font-medium sm:px-3 md:px-5 py-3 sm:py-5 md:py-2 hover:bg-gray-900 hover:scale-95 rounded-full md:rounded m-0 md:m-0 md:w-auto w-full"
          >
            <div>
              Add operation <span className="text-green-500 font-extrabold text-md">+</span>
            </div>
          </button>

          <button
            type="button"
            onClick={toggleTheme}
            className={`bg-black dark:bg-gray-200 text-md ${isDark ? 'text-black' : 'text-white'} dark:text-black p-3 rounded-full transition m-0 md:m-0 md:w-auto w-full`}
          >
            {isDark ? 'Light mode' : 'Dark mode'}
          </button>
        </div>
      </div>
    </header>
  )
}


