export class Api {
  async #fetch(pathOrUrl: string, options?: RequestInit) {
    const url =
      typeof pathOrUrl === "string" && pathOrUrl.startsWith("http")
        ? pathOrUrl
        : "http://localhost:3000" + pathOrUrl;
    const r = await fetch(url, options);

    if (!r.ok) {
      throw new Error(`HTTP is not caught: ${url}, error status ${r.status}`);
    }
    return await r.json();
  }

  async getTransactions(params?: string | Record<string, string>) {
    if (!params) {
      return this.#fetch("/transactions");
    }
    const query =
      typeof params === "string"
        ? params
        : new URLSearchParams(params).toString();
    return this.#fetch(`/transactions?${query}`);
  }

  async deleteTransaction(id: string | number) {
    return this.#fetch(`/transactions/${id}`, { method: "DELETE" });
  }

  async updateTransaction(id: string | number, updates: Record<string, unknown>) {
    return this.#fetch(`/transactions/${id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(updates),
    });
  }

  async getAllTransactions() {
    return this.#fetch("/transactions");
  }

  async getTransaction(id: string | number) {
    return this.#fetch(`/transactions/${id}`);
  }

  async getCategories() {
    return this.#fetch("/categories");
  }

  async createTransaction(data: Record<string, unknown>) {
    return this.#fetch("/transactions", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
  }

  async createCategory(data: Record<string, unknown>) {
    return this.#fetch("/categories", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    });
  }
}

// Export a singleton instance for convenience
export const api = new Api();

// Default export for convenience
export default api;
