"use client"

import { useState } from 'react'
import api from '../services/logic'

interface TransactionFormData {
  amount: number
  category: string
  description: string
  type: 'expense' | 'income'
  date: string
}

export default function CreateTransaction() {
  const [formData, setFormData] = useState<TransactionFormData>({
    amount: 0,
    category: '1',
    description: '',
    type: 'expense',
    date: new Date().toISOString().split('T')[0], // Today's date in YYYY-MM-DD format
  })
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setMessage(null)

    try {
      // Create the transaction using the API
      const newTransaction = await api.createTransaction({
        amount: Number(formData.amount),
        category: formData.category,
        description: formData.description,
        type: formData.type,
        date: new Date(formData.date).toISOString(),
      })

      setMessage({
        type: 'success',
        text: `Transaction created successfully! ID: ${newTransaction.id}`,
      })

      // Reset form
      setFormData({
        amount: 0,
        category: '1',
        description: '',
        type: 'expense',
        date: new Date().toISOString().split('T')[0],
      })
    } catch (error) {
      setMessage({
        type: 'error',
        text: error instanceof Error ? error.message : 'Failed to create transaction',
      })
    } finally {
      setLoading(false)
    }
  }

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target
    setFormData((prev) => ({
      ...prev,
      [name]: name === 'amount' ? parseFloat(value) || !0 : value,
    }))
  }

  return (
    <div className="max-w-md mx-auto p-4 bg-white dark:bg-gray-800 rounded-lg shadow-lg">
      <h2 className="text-xl font-bold mb-4 text-black dark:text-white">
        Create New Transaction
      </h2>

      <form onSubmit={handleSubmit} className="space-y-3">
        <div>
          <label
            htmlFor="type"
            className="block text-sm font-medium text-black dark:text-white mb-1"
          >
            Type
          </label>
          <select
            id="type"
            name="type"
            value={formData.type}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md bg-white dark:bg-gray-700 dark:text-white dark:border-gray-600"
            required
          >
            <option value="expense" className="dark:bg-gray-700 dark:text-white">Expense</option>
            <option value="income" className="dark:bg-gray-700 dark:text-white">Income</option>
          </select>
        </div>

        <div>
          <label
            htmlFor="amount"
            className="block text-sm font-medium text-black dark:text-white mb-1"
          >
            Amount
          </label>
          <input
            type="number"
            id="amount"
            name="amount"
            value={formData.amount}
            onChange={handleChange}
            min="0"
            step="0.01"
            className="w-full p-2 border border-gray-300 rounded-md bg-white dark:bg-gray-700 dark:text-white dark:border-gray-600"
            required
          />
        </div>

        <div>
          <label
            htmlFor="category"
            className="block text-sm font-medium text-black dark:text-white mb-1"
          >
            Category
          </label>
          <select
            id="category"
            name="category"
            value={formData.category}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md bg-white dark:bg-gray-700 dark:text-white dark:border-gray-600"
            required
          >
            <option value="1" className="dark:bg-gray-700 dark:text-white">Food</option>
            <option value="2" className="dark:bg-gray-700 dark:text-white">Salary</option>
          </select>
        </div>

        <div>
          <label
            htmlFor="description"
            className="block text-sm font-medium text-black dark:text-white mb-1"
          >
            Description
          </label>
          <textarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            rows={3}
            className="w-full p-2 border border-gray-300 rounded-md bg-white dark:bg-gray-700 dark:text-white dark:border-gray-600"
            required
          />
        </div>

        <div>
          <label
            htmlFor="date"
            className="block text-sm font-medium text-black dark:text-white mb-1"
          >
            Date
          </label>
          <input
            type="date"
            id="date"
            name="date"
            value={formData.date}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md bg-white dark:bg-gray-700 dark:text-white dark:border-gray-600"
            required
          />
        </div>

        {message && (
          <div
            className={`p-3 rounded-md ${
              message.type === 'success'
                ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
                : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
            }`}
          >
            {message.text}
          </div>
        )}

        <button
          type="submit"
          disabled={loading}
          className="w-full bg-blue-500 hover:bg-blue-600 disabled:bg-gray-400 text-white font-medium py-2 px-4 rounded-md transition-colors"
        >
          {loading ? 'Creating...' : 'Create Transaction'}
        </button>
      </form>
    </div>
  )
}

